"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { Map, Navigation, AlertTriangle, Info, Menu, X, User, Plane } from "lucide-react";
import { useState } from "react";
import { usePassenger } from "@/lib/context/PassengerContext";
import { passengerClassColors, theme } from "@/lib/config/theme";

const navItems = [
  { name: "Carte", href: "/map", icon: Map },
  { name: "Navigation", href: "/navigation", icon: Navigation },
  { name: "Signaler", href: "/report", icon: AlertTriangle },
  { name: "Services", href: "/services", icon: Info },
];

export default function Navbar() {
  const pathname = usePathname();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const { profile } = usePassenger();

  const classColor = profile
    ? passengerClassColors[profile.class].primary
    : theme.colors.gray[600];

  return (
    <>
      {/* Desktop Navbar */}
      <nav
        className="hidden md:block sticky top-0 z-50 backdrop-blur-sm"
        style={{
          backgroundColor: "rgba(255, 255, 255, 0.95)",
          borderBottom: `1px solid ${theme.colors.border.main}`,
          boxShadow: theme.shadow.sm,
        }}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center" style={{ height: "64px" }}>
            {/* Logo */}
            <Link
              href="/"
              className="flex items-center space-x-3 transition-transform hover:scale-105"
            >
              <div
                className="w-10 h-10 rounded-full flex items-center justify-center"
                style={{
                  background: `linear-gradient(135deg, ${theme.colors.accent.main} 0%, ${theme.colors.accent.dark} 100%)`,
                  boxShadow: theme.shadow.md,
                }}
              >
                <Plane className="w-6 h-6 text-white" />
              </div>
              <div>
                <h1
                  className="font-semibold"
                  style={{
                    fontSize: theme.typography.h6.fontSize,
                    fontWeight: theme.typography.h6.fontWeight,
                    color: theme.colors.text.primary,
                    lineHeight: 1.2,
                  }}
                >
                  SafeTraveler
                </h1>
                <p
                  className="text-xs"
                  style={{
                    color: theme.colors.text.secondary,
                    fontSize: theme.typography.tiny.fontSize,
                  }}
                >
                  Aéroport de Lomé
                </p>
              </div>
            </Link>

            {/* Nav Items */}
            <div className="flex items-center" style={{ gap: theme.spacing[1] }}>
              {navItems.map((item) => {
                const Icon = item.icon;
                const isActive = pathname === item.href;
                return (
                  <Link
                    key={item.name}
                    href={item.href}
                    className="flex items-center space-x-2 rounded-lg transition-all"
                    style={{
                      padding: `${theme.spacing[2]} ${theme.spacing[4]}`,
                      backgroundColor: isActive
                        ? theme.colors.accent.main + "15"
                        : "transparent",
                      color: isActive
                        ? theme.colors.accent.dark
                        : theme.colors.text.secondary,
                      fontSize: theme.typography.small.fontSize,
                      fontWeight: isActive ? 600 : 500,
                    }}
                    onMouseEnter={(e) => {
                      if (!isActive) {
                        e.currentTarget.style.backgroundColor = theme.colors.gray[100];
                      }
                    }}
                    onMouseLeave={(e) => {
                      if (!isActive) {
                        e.currentTarget.style.backgroundColor = "transparent";
                      }
                    }}
                  >
                    <Icon className="w-5 h-5" />
                    <span>{item.name}</span>
                  </Link>
                );
              })}
            </div>

            {/* Profile Badge */}
            {profile && (
              <div
                className="flex items-center space-x-3 rounded-lg"
                style={{
                  padding: `${theme.spacing[2]} ${theme.spacing[4]}`,
                  backgroundColor: theme.colors.gray[50],
                  border: `1px solid ${theme.colors.border.main}`,
                }}
              >
                <div
                  className="w-9 h-9 rounded-full flex items-center justify-center"
                  style={{
                    backgroundColor: classColor + "15",
                    border: `2px solid ${classColor}`,
                  }}
                >
                  <User className="w-5 h-5" style={{ color: classColor }} />
                </div>
                <div className="text-left">
                  <p
                    className="font-medium"
                    style={{
                      fontSize: theme.typography.small.fontSize,
                      color: theme.colors.text.primary,
                    }}
                  >
                    {profile.class === "economy"
                      ? "Économie"
                      : profile.class === "business"
                      ? "Business"
                      : "First Class"}
                  </p>
                  {profile.flightNumber && (
                    <p
                      className="font-semibold"
                      style={{
                        fontSize: theme.typography.tiny.fontSize,
                        color: classColor,
                      }}
                    >
                      {profile.flightNumber}
                    </p>
                  )}
                </div>
              </div>
            )}
          </div>
        </div>
      </nav>

      {/* Mobile Navbar */}
      <nav
        className="md:hidden sticky top-0 z-50 backdrop-blur-sm"
        style={{
          backgroundColor: "rgba(255, 255, 255, 0.95)",
          borderBottom: `1px solid ${theme.colors.border.main}`,
          boxShadow: theme.shadow.sm,
        }}
      >
        <div className="px-4">
          <div className="flex justify-between items-center" style={{ height: "56px" }}>
            <Link href="/" className="flex items-center space-x-2">
              <div
                className="w-9 h-9 rounded-full flex items-center justify-center"
                style={{
                  background: `linear-gradient(135deg, ${theme.colors.accent.main} 0%, ${theme.colors.accent.dark} 100%)`,
                  boxShadow: theme.shadow.sm,
                }}
              >
                <Plane className="w-5 h-5 text-white" />
              </div>
              <span
                className="font-semibold"
                style={{
                  color: theme.colors.text.primary,
                  fontSize: theme.typography.base.fontSize,
                }}
              >
                SafeTraveler
              </span>
            </Link>

            <button
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              className="p-2 rounded-lg transition-colors"
              style={{
                color: theme.colors.text.primary,
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.backgroundColor = theme.colors.gray[100];
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.backgroundColor = "transparent";
              }}
              aria-label="Menu"
            >
              {mobileMenuOpen ? (
                <X className="w-6 h-6" />
              ) : (
                <Menu className="w-6 h-6" />
              )}
            </button>
          </div>
        </div>

        {/* Mobile Menu Dropdown */}
        {mobileMenuOpen && (
          <div
            className="border-t"
            style={{
              borderColor: theme.colors.border.main,
              backgroundColor: theme.colors.background.default,
            }}
          >
            <div className="px-4 py-2 space-y-1">
              {navItems.map((item) => {
                const Icon = item.icon;
                const isActive = pathname === item.href;
                return (
                  <Link
                    key={item.name}
                    href={item.href}
                    onClick={() => setMobileMenuOpen(false)}
                    className="flex items-center space-x-3 rounded-lg transition-colors"
                    style={{
                      padding: `${theme.spacing[3]} ${theme.spacing[3]}`,
                      backgroundColor: isActive
                        ? theme.colors.accent.main + "15"
                        : "transparent",
                      color: isActive
                        ? theme.colors.accent.dark
                        : theme.colors.text.secondary,
                      fontSize: theme.typography.base.fontSize,
                      fontWeight: isActive ? 600 : 500,
                    }}
                  >
                    <Icon className="w-5 h-5" />
                    <span>{item.name}</span>
                  </Link>
                );
              })}

              {/* Profile in mobile menu */}
              {profile && (
                <div
                  className="flex items-center space-x-3 rounded-lg mt-2"
                  style={{
                    padding: `${theme.spacing[3]} ${theme.spacing[3]}`,
                    backgroundColor: theme.colors.gray[50],
                    border: `1px solid ${theme.colors.border.main}`,
                  }}
                >
                  <div
                    className="w-9 h-9 rounded-full flex items-center justify-center"
                    style={{
                      backgroundColor: classColor + "15",
                      border: `2px solid ${classColor}`,
                    }}
                  >
                    <User className="w-5 h-5" style={{ color: classColor }} />
                  </div>
                  <div>
                    <p
                      className="font-medium"
                      style={{
                        fontSize: theme.typography.small.fontSize,
                        color: theme.colors.text.primary,
                      }}
                    >
                      {profile.class === "economy"
                        ? "Économie"
                        : profile.class === "business"
                        ? "Business"
                        : "First Class"}
                    </p>
                    {profile.flightNumber && (
                      <p
                        className="font-semibold"
                        style={{
                          fontSize: theme.typography.tiny.fontSize,
                          color: classColor,
                        }}
                      >
                        {profile.flightNumber}
                      </p>
                    )}
                  </div>
                </div>
              )}
            </div>
          </div>
        )}
      </nav>

      {/* Mobile Bottom Navigation */}
      <div
        className="md:hidden fixed bottom-0 left-0 right-0 z-50 backdrop-blur-sm"
        style={{
          backgroundColor: "rgba(255, 255, 255, 0.98)",
          borderTop: `1px solid ${theme.colors.border.main}`,
          boxShadow: `0 -${theme.shadow.md}`,
        }}
      >
        <div className="grid grid-cols-4" style={{ height: "64px" }}>
          {navItems.map((item) => {
            const Icon = item.icon;
            const isActive = pathname === item.href;
            return (
              <Link
                key={item.name}
                href={item.href}
                className="flex flex-col items-center justify-center transition-all"
                style={{
                  color: isActive
                    ? theme.colors.accent.main
                    : theme.colors.text.secondary,
                }}
              >
                <Icon
                  className="w-6 h-6"
                  style={{
                    strokeWidth: isActive ? 2.5 : 2,
                  }}
                />
                <span
                  className="mt-1"
                  style={{
                    fontSize: theme.typography.tiny.fontSize,
                    fontWeight: isActive ? 600 : 400,
                  }}
                >
                  {item.name}
                </span>
              </Link>
            );
          })}
        </div>
      </div>
    </>
  );
}
